import PopupWithForm from './PopupWithForm';

function ConfirmationPopup({ isOpen, onClose, onConfirm, isLoading }) {
  function handleSubmit(e) {
    e.preventDefault();

    onConfirm();
  }

  return (
    <PopupWithForm
      name='confirmation'
      title=' ?'
      buttonText=''
      loadingText='...'
      onClose={onClose}
      onSubmit={handleSubmit}
      isOpen={isOpen}
      isLoading={isLoading}
      isValid={true}
    />
  );
}

export default ConfirmationPopup;
